import { Injectable } from '@angular/core';
import { AngularFirestore,AngularFirestoreCollection,AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import {Film} from '../film';
import {Comment} from '../comments';
import { firestore } from 'firebase/app';
import { FirebaseApp } from 'angularfire2';
@Injectable()


export class ListfilmService {
  filmCollection :AngularFirestoreCollection<Film>;
  commentCollection :AngularFirestoreCollection<Comment>;
  comment:Observable<Comment[]>;

  film:Observable<Film[]>;
  constructor(private db: AngularFirestore) {
    this.filmCollection=this.db.collection<Film>('film');
    this.commentCollection=this.db.collection<Comment>('message');
   }
   

   getComments()
  {   
     this.comment=this.commentCollection.valueChanges();
     return this.comment;
   }
   getfilms()
   {
    
   this.film=this.filmCollection.valueChanges();
   return this.film;
   }
   updatefilm1(film3 :Film)
{

  this.filmCollection.doc(film3.id).update({
    nom:film3.nom,
    type:film3.type,
    description:film3.description,
    id:film3.id,
    jaime:film3.jaime+1,
    image:film3.image

  })
  
}
addmessage(com : Comment)
{
this.commentCollection.add(com).then((docRef) => {
  this.commentCollection.doc(docRef.id).update({
    id: docRef.id,
    
  })
})
}
}



